.homePage {
  width: 100%;
  font-family: sans-serif;
  background: rgb(4, 46, 0);
  background: linear-gradient(
    135deg,
    rgba(5, 79, 17, 0.99) 9%,
    rgba(0, 0, 0, 1) 50%,
    rgba(5, 62, 15, 1) 93%
  ); 
  box-shadow: 0px 4px 60px -1px rgba(0, 0, 0, 0.2),
    inset 0px 4px 50px rgba(0, 0, 0, 0.25);
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}

.landingPageCover__animation {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  opacity: 0.1;
  width: 100vw;
}
