.landingPage {
  width: 80%;
  height: calc(100vh - 4em);
  opacity: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 0px auto;
  padding: 2.5em;
}

.main_img {
  width: 25vw;
  height: 38vh;
}

.innovationIn {
  display: flex;
  padding: 0;
  position: relative;
  top: 3em;
}
.innovation {
  color: rgb(229, 229, 229);
  display: flex;
  font-size: 60px;
  text-shadow: 15px 15px 2px #00000044;
  padding: 0;
}

.O {
  width: 250px;
  border: 10px solid rgb(255, 255, 255);
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  border-top-right-radius: 20px;
  border-top-left-radius: 20px;
  height: 69px;
  margin: 19px;
  padding: 0;
}
#in {
  color: #fff;
  padding-top: 5vh;
  margin-left: 15px;
  font-size: 80px;
  font-weight: bolder;
}
#agriculture {
  font-size: 140px;
  color: #2de230;
  margin: 0;
  padding: 10px;
}
.landingText {
  font-size: 18px;
  width: 80%;
  color: #fff;
  text-align: center;
  line-height: 1.5;
}
.landingButtons {
  font-size: large;
}
#signUpButton {
  background: linear-gradient(89.89deg, #003805 0.06%, #467814 102.53%);
  border-radius: 13px;
}
#aboutUsButton {
  background: linear-gradient(
    113.23deg,
    rgb(54 48 48 / 76%) 34.99%,
    rgba(255, 255, 255, 0) 116.05%
  );
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  box-shadow: 0px 4px 60px -1px rgba(0, 0, 0, 0.2),
    inset 0px 4px 50px rgba(0, 0, 0, 0.25);
  border-radius: 19px;
}

/* expanded landing page */
.main_container {
  padding-top: 160px;
  margin: 0px auto;
  width: 85%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.containerDomain {
  width: 25%;
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.containerWorkshops {
  width: 25%;
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.containerHeading {
  font-size: 45px;
  margin: 10px 0px;
}
.containerText {
  font-size: 18px;
  font-weight: lighter;
  line-height: 1.5;
}
.learnMoreButton {
  background: linear-gradient(
    113.23deg,
    rgba(255, 255, 255, 0.3) 34.99%,
    rgba(255, 255, 255, 0) 116.05%
  );
  box-shadow: 0px 4px 60px -1px rgba(0, 0, 0, 0.2),
    inset 0px 4px 50px rgba(0, 0, 0, 0.25);
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  border-radius: 19px;
  border-radius: 29px;
}

/* trailer */

#trailer {
  font-size: 72px;
  color: #74eb76;
  margin: 30px 0px;
}
.trailer {
  padding-top: 7vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  flex-direction: column;
}
.trailerBox {
  width: 60vw;
  height: 60vh;
  border: 6px solid #74eb76;
  display: flex;
  padding: 1%;
  justify-content: center;
  align-items: center;
  border-radius: 63px;
}
.trailerVid {
  width: 60vw;
  height: 60vh;
  border-radius: 60px;
}
/* sponsor */
#sponsor {
  text-align: center;
  margin-top: 30px;
  font-size: 72px;
}
.sponsor {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  flex-direction: column;
}
.sponsorImages {
  width: 100%;
  display: flex;
  height: 65vh;
  justify-content: space-around;
  align-items: center;
}
.prevSlide {
  margin-left: 7%;
  width: 20vw;
  height: 25vh;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}
.mainSlide {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 60vh;
  box-shadow: 40px 40px 15px 0px rgba(45, 226, 48, NaN);
  width: 30vw;
  position: relative;
  overflow: hidden;
}
.nextSlide {
  overflow: hidden;
  margin-right: 7%;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20vw;
  height: 25vh;
}
.backToTop {
  background-color: transparent;
  display: inline;
  float: right;
  width: 10vw;
}

/* animations */
@keyframes scale-up-center {
  0% {
    transform: scale(0.5);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes rotate {
  0% {
    transform: perspective(1000px) rotateY(0deg);
  }
  100% {
    transform: perspective(1000px) rotateY(360deg);
  }
}
